}

.menu-btn span {
  width: 25px;
  height: 3px;
  background-color: #fff;
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  transform: translate(0, -50%);
}

.menu-btn span:before {
  content: "";
  width: 25px;
  height: 3px;
  background-color: #fff;
  display: block;
  position: absolute;
  top: 8px;
  left: 0;
}

.menu-btn span:after {
  content: "";
  width: 25px;
  height: 3px;
  background-color: #fff;
  display: block;
  position: absolute;
  bottom: 8px;
  left: 0;
}

